import React from 'react';
import DefaultLayout from './containers/DefaultLayout';

const Dashboard = React.lazy (() => import ('./views/Init'));
const Measurements = React.lazy (() => import ('./views/Measurements'));
const Client = React.lazy (() => import ('./views/Client'));
const ClientsForm = React.lazy (() => import ('./views/Client/ClientForm'));
const TipoUsuarios = React.lazy (() => import ('./views/TipoUsuarios'));
const TipoUsuariosForm = React.lazy (() =>
  import ('./views/TipoUsuarios/TipoUsuariosForm')
);
const Subsidiary = React.lazy (() => import ('./views/Subsidiary'));
const SubsidiaryForm = React.lazy (() =>
  import ('./views/Subsidiary/SubsidiaryForm')
);
const Measurement = React.lazy (() => import ('./views/Measurement'));
const Invoice = React.lazy (() => import ('./views/Invoice'));
const InvoiceForm = React.lazy (() => import ('./views/Invoice/InvoiceForm'));
const TransferableFixed = React.lazy (() =>
  import ('./views/TransferableFixed')
);
const TransferableFixedForm = React.lazy (() =>
  import ('./views/TransferableFixed/TransferableFixedForm')
);
const DistributionDivision = React.lazy (() =>
  import ('./views/DistributionDivision')
);
const DistributionDivisionForm = React.lazy (() =>
  import ('./views/DistributionDivision/DistributionDivisionForm')
);

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  {path: '/', exact: true, name: 'IPCO', component: DefaultLayout},
  {path: '/inicio', name: 'Inicio', component: Dashboard},

  {
    path: '/clientes/:id',
    name: 'Clientes',
    component: ClientsForm,
  },
  {
    path: '/clientes',
    name: 'Lista de Clientes',
    component: Client,
  },
  {
    path: '/tipos/:id',
    name: 'Tipos de usuarios',
    component: TipoUsuariosForm,
  },
  {
    path: '/tipos',
    name: 'Lista de Tipos de usuarios',
    component: TipoUsuarios,
  },
  {
    path: '/centros-carga/:id',
    name: 'centros-carga',
    component: SubsidiaryForm,
  },
  {
    path: '/centros-carga',
    name: 'Lista de centros de carga',
    component: Subsidiary,
  },
  {path: '/medidor', name: 'Medidor', component: Measurement},
  {path: '/mediciones', name: 'Mediciones', component: Measurements},
  {
    path: '/facturacion/:id',
    name: 'Facturación',
    component: InvoiceForm,
  },
  {
    path: '/facturacion',
    name: 'Lista de facturas',
    component: Invoice,
  },
  {
    path: '/trasladables-fijos/:id',
    name: 'Trasladables fijos',
    component: TransferableFixedForm,
  },
  {
    path: '/trasladables-fijos',
    name: 'Lista de trasladables fijos',
    component: TransferableFixed,
  },
  {
    path: '/division-distribucion/:id',
    name: 'División de Distribuciones',
    component: DistributionDivisionForm,
  },
  {
    path: '/division-distribucion',
    name: 'Lista de División de Distribuciones',
    component: DistributionDivision,
  },
];

export default routes;
